import Vue from 'vue'
import VueRouter from 'vue-router'
import { genCompRoutes } from './routes'

Vue.use(VueRouter)

export default new VueRouter({
  // mode: 'history',
  routes: [
    {
      path: '/',
      component: () => import(`@/views/bar`)
    },
    ...genCompRoutes()
  ]
})
